.homepage-intro {
  display: flex;
  align-items: center;
  padding: $space-xl $space-md;
  background: #f9f9f9;  // light background for contrast with hero

  .intro-text {
    flex: 1 1 50%;
    font-size: $font-size-body;
    line-height: 1.5;
    margin-right: $space-lg;
  }
  .intro-images {
    flex: 1 1 50%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: $space-sm;
  }
  .intro-images img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border: 2px solid #eee;
    /* Ensure each image has alt text in HTML for accessibility */
  }

  @media (max-width: $breakpoint-md) {
    flex-direction: column;
    .intro-text, .intro-images {
      flex: 1 1 100%;
      max-width: 100%;
      margin: 0 0 $space-md 0;
    }
    .intro-images {
      grid-template-columns: 1fr 1fr; /* still two per row on tablet */
    }
  }
  @media (max-width: $breakpoint-sm) {
    .intro-images {
      grid-template-columns: 1fr; /* one per row on narrow mobile screens */
    }
  }
}

.homepage-highlights {
  padding: $space-xl $space-md;
  background: #fff;

  h2 {
    text-align: center;
    font-size: 1.75rem;
    margin-bottom: $space-lg;
  }
  /* Carousel container (assume the block gives a .item-carousel class or similar) */
  .item-carousel {
    display: flex;
    overflow-x: auto;
    scroll-behavior: smooth;
    padding-bottom: $space-sm;
    margin: 0 -$space-sm;
  }
  .item-carousel .carousel-item {
    flex: 0 0 250px;
    margin: 0 $space-sm;
    background: #f1f1f1;
    border-radius: 4px;
    text-align: center;
  }
  .carousel-item img {
    width: 100%;
    height: auto;
    display: block;
  }
  .carousel-item .item-title {
    font-size: $font-size-body-sm;
    padding: $space-sm;
  }

  /* Example arrow controls if present */
  .carousel-control-prev, .carousel-control-next {
    background: rgba(0,0,0,0.5);
    color: #fff;
    border: none;
    width: 2rem;
    height: 2rem;
  }
}
//------ MAP ------ //
.homepage-map {
  padding: $space-xl 0;
  background: #eef5fa;  // light blue-ish background to signify geographic section

  h2 {
    text-align: center;
    font-size: 1.75rem;
    margin-bottom: $space-lg;
  }
  .mapping-block, .map-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  .mapping-block iframe, .mapping-block .map-canvas {
    width: 100%;
    min-height: 400px;
    border: 0;
  }

  @media (max-width: $breakpoint-md) {
    padding: $space-lg 0;
    .mapping-block iframe, .mapping-block .map-canvas {
      min-height: 300px;
    }
  }
}

.homepage-funds {
  padding: $space-xl $space-md $space-lg;
  background: #fff;

  h2 {
    text-align: center;
    font-size: 1.75rem;
    margin-bottom: $space-lg;
  }
  .funds-grid {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: $space-md;
  }
  .fund-card {
    background: #fafafa;
    border: 1px solid #ddd;
    border-radius: 4px;
    text-align: center;
    padding: $space-md;
    transition: background 0.3s;
  }
  .fund-card:hover, .fund-card:focus-within {
    background: #f0f8ff;  // highlight on hover/focus
  }
  .fund-card img {
    max-width: 100%;
    height: auto;
    margin-bottom: $space-sm;
  }
  .fund-card h3 {
    font-size: 1.2rem;
    margin: 0 0 $space-sm;
  }
  .fund-card p {
    font-size: $font-size-body-sm;
    color: #555;
  }

  @media (max-width: $breakpoint-lg) {
    .funds-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media (max-width: $breakpoint-sm) {
    .funds-grid {
      grid-template-columns: 1fr;
    }
  }
}
