.homepage-hero {
  background: url('../images/hero-bg.jpg') center center / cover no-repeat;
  padding: $space-xl $space-xl 0;  // generous top padding for hero; bottom padding may be added below content
  color: #fff;                    // assume white text over dark background

  .hero-inner {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
    padding-bottom: $space-xl;
  }
  h1, .block-html h1 {
    font-size: 2.5rem;
    margin-bottom: $space-lg;
  }
  .block-united-search-form { /* assuming UnitedSearch block outputs a form with this class */
    max-width: 600px;
    margin: 1.5rem auto 0;
  }

  // Responsive adjustments for smaller screens
  @media (max-width: $breakpoint-md) {
    .hero-inner {
      padding: $space-lg $space-md;  // reduce side padding on tablet/mobile
    }
    h1 {
      font-size: 1.8rem;
    }
  }
}
